'use client';

import { useState, useEffect, useRef } from 'react';
import { usePrivy } from '@privy-io/react-auth';
import { supabase } from '@/lib/supabase';

interface SearchBoxProps {
  onPickupChange?: (location: string, coords?: { lat: number; lng: number }) => void;
  onDropoffChange?: (location: string, coords?: { lat: number; lng: number }) => void;
}

export default function SearchBox({ onPickupChange, onDropoffChange }: SearchBoxProps) {
  const { user, authenticated, login } = usePrivy();
  const [step, setStep] = useState(1); // 1: Âú∞ÁÇπÈÄâÊã©, 2: Êó∂Èó¥ÂíåÂæÆ‰ø°, 3: Á°ÆËÆ§
  const [pickup, setPickup] = useState('');
  const [dropoff, setDropoff] = useState('');
  const [pickupCoords, setPickupCoords] = useState<{ lat: number; lng: number } | null>(null);
  const [dropoffCoords, setDropoffCoords] = useState<{ lat: number; lng: number } | null>(null);
  const [wechatId, setWechatId] = useState('');
  const [departureTime, setDepartureTime] = useState('');
  const [loading, setLoading] = useState(false);
  const [locating, setLocating] = useState(false);
  const [mapsLoaded, setMapsLoaded] = useState(false);

  const pickupInputRef = useRef<HTMLInputElement>(null);
  const dropoffInputRef = useRef<HTMLInputElement>(null);
  const pickupAutocompleteRef = useRef<google.maps.places.Autocomplete | null>(null);
  const dropoffAutocompleteRef = useRef<google.maps.places.Autocomplete | null>(null);

  // Wait for Google Maps to load
  useEffect(() => {
    const checkGoogleMaps = () => {
      if (window.google && window.google.maps && window.google.maps.places) {
        setMapsLoaded(true);
        return true;
      }
      return false;
    };

    // Check immediately
    if (checkGoogleMaps()) {
      return;
    }

    // Check periodically
    const interval = setInterval(() => {
      if (checkGoogleMaps()) {
        clearInterval(interval);
      }
    }, 500);

    return () => clearInterval(interval);
  }, []);

  // Initialize Google Places Autocomplete
  useEffect(() => {
    if (!mapsLoaded || !window.google || !window.google.maps || !window.google.maps.places) {
      return;
    }

    // Setup pickup autocomplete
    if (pickupInputRef.current && !pickupAutocompleteRef.current) {
      pickupAutocompleteRef.current = new google.maps.places.Autocomplete(
        pickupInputRef.current,
        {
          componentRestrictions: { country: 'us' },
          fields: ['formatted_address', 'geometry', 'name'],
          types: ['establishment', 'geocode'],
        }
      );

      pickupAutocompleteRef.current.addListener('place_changed', () => {
        const place = pickupAutocompleteRef.current?.getPlace();
        if (place && place.geometry && place.geometry.location) {
          const address = place.formatted_address || place.name || '';
          const coords = {
            lat: place.geometry.location.lat(),
            lng: place.geometry.location.lng(),
          };
          setPickup(address);
          setPickupCoords(coords);
          if (onPickupChange) {
            onPickupChange(address, coords);
          }
        }
      });
    }

    // Setup dropoff autocomplete
    if (dropoffInputRef.current && !dropoffAutocompleteRef.current) {
      dropoffAutocompleteRef.current = new google.maps.places.Autocomplete(
        dropoffInputRef.current,
        {
          componentRestrictions: { country: 'us' },
          fields: ['formatted_address', 'geometry', 'name'],
          types: ['establishment', 'geocode'],
        }
      );

      dropoffAutocompleteRef.current.addListener('place_changed', () => {
        const place = dropoffAutocompleteRef.current?.getPlace();
        if (place && place.geometry && place.geometry.location) {
          const address = place.formatted_address || place.name || '';
          const coords = {
            lat: place.geometry.location.lat(),
            lng: place.geometry.location.lng(),
          };
          setDropoff(address);
          setDropoffCoords(coords);
          if (onDropoffChange) {
            onDropoffChange(address, coords);
          }
        }
      });
    }
  }, [mapsLoaded, onPickupChange, onDropoffChange]);

  // Geolocation: Get current position
  const handleGetCurrentLocation = async () => {
    if (!navigator.geolocation) {
      alert('Âú∞ÁêÜÂÆö‰Ωç‰∏çÊîØÊåÅ / Geolocation not supported');
      return;
    }

    // Check if Google Maps API is loaded
    if (!window.google || !window.google.maps) {
      alert('Âú∞ÂúñÂä†Ëºâ‰∏≠ÔºåË´ãÁ®çÂæåÂÜçË©¶ / Map is loading, please try again');
      return;
    }

    setLocating(true);
    navigator.geolocation.getCurrentPosition(
      async (position) => {
        const { latitude, longitude } = position.coords;

        try {
          // Double check Google Maps is available
          if (window.google && window.google.maps) {
            const geocoder = new google.maps.Geocoder();
            const result = await geocoder.geocode({
              location: { lat: latitude, lng: longitude },
            });

            if (result.results && result.results.length > 0) {
              const address = result.results[0].formatted_address;
              const coords = { lat: latitude, lng: longitude };
              setPickup(address);
              setPickupCoords(coords);
              if (onPickupChange) {
                onPickupChange(address, coords);
              }
            }
          } else {
            throw new Error('Google Maps not loaded');
          }
        } catch (error) {
          console.error('Geocoding error:', error);
          const locationString = `${latitude.toFixed(4)}, ${longitude.toFixed(4)}`;
          const coords = { lat: latitude, lng: longitude };
          setPickup(locationString);
          setPickupCoords(coords);
          if (onPickupChange) {
            onPickupChange(locationString, coords);
          }
        } finally {
          setLocating(false);
        }
      },
      (error) => {
        console.error('Geolocation error:', error);
        alert('Êó†Ê≥ïËé∑Âèñ‰ΩçÁΩÆ / Unable to get location');
        setLocating(false);
      },
      {
        enableHighAccuracy: true,
        timeout: 5000,
        maximumAge: 0,
      }
    );
  };

  const handleContinue = async () => {
    if (!pickup || !dropoff) {
      alert('Ë´ãËº∏ÂÖ•Ëµ∑ÈªûÂíåÁµÇÈªû / Please enter pickup and dropoff locations');
      return;
    }

    // If we don't have coordinates, try to geocode the addresses
    if (!pickupCoords || !dropoffCoords) {
      if (!mapsLoaded || !window.google || !window.google.maps) {
        alert('Âú∞ÂúñËºâÂÖ•‰∏≠ÔºåË´ãÁ®çÂæåÂÜçË©¶ÔºàÁ¥ÑÈúÄ 2-5 ÁßíÔºâ\nMap is loading, please wait (2-5 seconds)');
        return;
      }

      setLoading(true);
      const geocoder = new google.maps.Geocoder();

      try {
        // Geocode pickup if needed
        if (!pickupCoords && pickup) {
          const pickupResult = await geocoder.geocode({ address: pickup });
          if (pickupResult.results && pickupResult.results.length > 0) {
            const location = pickupResult.results[0].geometry.location;
            const coords = { lat: location.lat(), lng: location.lng() };
            setPickupCoords(coords);
            if (onPickupChange) {
              onPickupChange(pickup, coords);
            }
          } else {
            alert('Êâæ‰∏çÂà∞Ëµ∑Èªû‰ΩçÁΩÆÔºåË´ãÈáçÊñ∞Ëº∏ÂÖ• / Cannot find pickup location');
            setLoading(false);
            return;
          }
        }

        // Geocode dropoff if needed
        if (!dropoffCoords && dropoff) {
          const dropoffResult = await geocoder.geocode({ address: dropoff });
          if (dropoffResult.results && dropoffResult.results.length > 0) {
            const location = dropoffResult.results[0].geometry.location;
            const coords = { lat: location.lat(), lng: location.lng() };
            setDropoffCoords(coords);
            if (onDropoffChange) {
              onDropoffChange(dropoff, coords);
            }
          } else {
            alert('Êâæ‰∏çÂà∞ÁµÇÈªû‰ΩçÁΩÆÔºåË´ãÈáçÊñ∞Ëº∏ÂÖ• / Cannot find dropoff location');
            setLoading(false);
            return;
          }
        }
      } catch (error) {
        console.error('Geocoding error:', error);
        alert('ÁÑ°Ê≥ïÁç≤ÂèñÂú∞ÈªûÂùêÊ®ôÔºåË´ãÂæû‰∏ãÊãâÂàóË°®‰∏≠ÈÅ∏Êìá / Please select from dropdown');
        setLoading(false);
        return;
      }

      setLoading(false);
    }

    setStep(2);
  };

  const handleBack = () => {
    if (step === 2) {
      setStep(1);
    }
  };

  const handleSubmit = async () => {
    if (!authenticated) {
      login();
      return;
    }

    if (!pickup || !dropoff || !wechatId) {
      alert('ËØ∑Â°´ÂÜôÊâÄÊúâÂøÖÂ°´Â≠óÊÆµ / Please fill in all required fields');
      return;
    }

    setLoading(true);
    try {
      const { error } = await supabase
        .from('rides')
        .insert([
          {
            user_address: user?.wallet?.address || user?.email?.address || 'unknown',
            pickup_location: pickup,
            dropoff_location: dropoff,
            departure_time: departureTime || new Date().toISOString(),
            wechat_id: wechatId,
            status: 'active',
          },
        ])
        .select()
        .single();

      if (error) {
        console.error('Supabase error:', error);
        alert(`Failed to create ride: ${error.message}`);
        throw error;
      }

      alert('Ë°åÁ®ãÂ∑≤ÂèëÂ∏ÉÔºÅAI Ê≠£Âú®ÂØªÊâæÊãºÂèã... / Ride published! AI is finding carpoolers...');

      // Reset form
      setPickup('');
      setDropoff('');
      setPickupCoords(null);
      setDropoffCoords(null);
      setWechatId('');
      setDepartureTime('');
      setStep(1);
    } catch (error: any) {
      console.error('Error creating ride:', error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <>
      {/* Step 1: Location Selection - Floating Card */}
      {step === 1 && (
        <div className="fixed top-4 left-4 right-4 z-20 max-w-2xl mx-auto">
          <div className="bg-white rounded-2xl shadow-2xl p-6">
            <h2 className="text-xl font-bold mb-4 text-gray-900">Ë®àÂäÉ‰Ω†ÁöÑË°åÁ®ã</h2>

            {/* Pickup location */}
            <div className="relative mb-3">
              <div className="absolute left-4 top-1/2 -translate-y-1/2 w-3 h-3 bg-green-500 rounded-full z-10"></div>
              <input
                ref={pickupInputRef}
                className="w-full pl-10 pr-12 py-4 bg-gray-50 border-2 border-gray-200 rounded-xl text-base text-gray-900 placeholder-gray-500 font-medium focus:outline-none focus:border-black focus:bg-white transition-all"
                placeholder="ÂæûÂì™Ë£°Âá∫ÁôºÔºü(‰æãÂ¶Ç: UCI, LAX)"
                value={pickup}
                onChange={(e) => setPickup(e.target.value)}
              />
              <button
                onClick={handleGetCurrentLocation}
                disabled={locating}
                className="absolute right-3 top-1/2 -translate-y-1/2 p-2 hover:bg-gray-200 rounded-full transition-colors disabled:opacity-50"
                title="‰ΩøÁî®Áï∂Ââç‰ΩçÁΩÆ"
              >
                {locating ? (
                  <svg className="animate-spin h-6 w-6 text-gray-700" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                  </svg>
                ) : (
                  <svg className="h-6 w-6 text-gray-700" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
                  </svg>
                )}
              </button>
            </div>

            {/* Dropoff location */}
            <div className="relative mb-5">
              <div className="absolute left-4 top-1/2 -translate-y-1/2 w-3 h-3 bg-red-500 rounded-full z-10"></div>
              <input
                ref={dropoffInputRef}
                className="w-full pl-10 pr-4 py-4 bg-gray-50 border-2 border-gray-200 rounded-xl text-base text-gray-900 placeholder-gray-500 font-medium focus:outline-none focus:border-black focus:bg-white transition-all"
                placeholder="Ë¶ÅÂéªÂì™Ë£°Ôºü(‰æãÂ¶Ç: Disneyland, Santa Monica)"
                value={dropoff}
                onChange={(e) => setDropoff(e.target.value)}
              />
            </div>

            {/* Continue button */}
            <button
              onClick={handleContinue}
              disabled={!pickup || !dropoff || loading}
              className="w-full py-4 bg-black text-white rounded-xl font-bold text-lg hover:bg-gray-800 transition-colors disabled:opacity-50 disabled:bg-gray-300 disabled:cursor-not-allowed"
            >
              {loading ? 'ËôïÁêÜ‰∏≠...' : 'ÁπºÁ∫å Continue'}
            </button>

            {/* Hint text */}
            <p className="text-xs text-gray-500 text-center mt-2">
              üí° ÊèêÁ§∫ÔºöË´ãÂæû‰∏ãÊãâÂàóË°®‰∏≠ÈÅ∏ÊìáÂú∞Èªû‰ª•Áç≤ÂæóÊúÄ‰Ω≥È´îÈ©ó
            </p>
          </div>
        </div>
      )}

      {/* Step 2: Time and WeChat - Bottom Sheet */}
      {step === 2 && (
        <div className="fixed bottom-0 left-0 right-0 bg-white rounded-t-3xl shadow-2xl z-10 max-w-full mx-auto">
          {/* Drag handle */}
          <div className="flex justify-center pt-3 pb-2">
            <div className="w-12 h-1 bg-gray-300 rounded-full"></div>
          </div>

          <div className="px-6 pb-8 pt-2">
            {/* Back button */}
            <button
              onClick={handleBack}
              className="flex items-center gap-2 text-gray-600 hover:text-black mb-4 font-medium"
            >
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
              </svg>
              ËøîÂõû
            </button>

            {/* Trip summary */}
            <div className="mb-6 p-4 bg-gray-50 rounded-xl">
              <div className="flex items-center gap-3 mb-2">
                <div className="w-3 h-3 bg-green-500 rounded-full"></div>
                <p className="text-sm text-gray-900 font-semibold">{pickup}</p>
              </div>
              <div className="flex items-center gap-3">
                <div className="w-3 h-3 bg-red-500 rounded-full"></div>
                <p className="text-sm text-gray-900 font-semibold">{dropoff}</p>
              </div>
            </div>

            <h2 className="text-2xl font-bold mb-6 text-gray-900">ÂÆåÊàêË°åÁ®ãË≥áË®ä</h2>

            {/* Departure time */}
            <div className="mb-4">
              <label className="block text-sm font-bold text-gray-900 mb-2">
                Âá∫ÁôºÊôÇÈñì Departure Time
              </label>
              <input
                type="datetime-local"
                className="w-full px-4 py-4 bg-gray-50 border-2 border-gray-200 rounded-xl text-base text-gray-900 placeholder-gray-500 font-medium focus:outline-none focus:border-black focus:bg-white transition-all"
                value={departureTime}
                onChange={(e) => setDepartureTime(e.target.value)}
              />
            </div>

            {/* WeChat ID */}
            <div className="mb-6">
              <label className="block text-sm font-bold text-gray-900 mb-2">
                ÂæÆ‰ø°Ëôü WeChat ID *
              </label>
              <input
                className="w-full px-4 py-4 bg-gray-50 border-2 border-gray-200 rounded-xl text-base text-gray-900 placeholder-gray-500 font-medium focus:outline-none focus:border-black focus:bg-white transition-all"
                placeholder="Ë´ãËº∏ÂÖ•‰Ω†ÁöÑÂæÆ‰ø°Ëôü"
                value={wechatId}
                onChange={(e) => setWechatId(e.target.value)}
              />
            </div>

            {/* Submit button */}
            <button
              onClick={handleSubmit}
              disabled={loading || !wechatId}
              className="w-full py-4 bg-black text-white rounded-xl font-bold text-lg hover:bg-gray-800 transition-colors disabled:opacity-50 disabled:bg-gray-300 disabled:cursor-not-allowed"
            >
              {loading ? 'ÁôºÂ∏É‰∏≠...' : authenticated ? 'ÁôºÂ∏ÉË°åÁ®ã ÈñãÂßãÂ∞ãÊâæÊãºÂèã' : 'ÁôªÂÖ•‰ª•ÁôºÂ∏ÉË°åÁ®ã'}
            </button>

            {!authenticated && (
              <p className="text-center text-sm text-gray-600 mt-3 font-medium">
                ÈúÄË¶ÅÁôªÂÖ•ÊâçËÉΩÁôºÂ∏ÉË°åÁ®ã / Login required to post rides
              </p>
            )}
          </div>
        </div>
      )}
    </>
  );
}
