'use client';

import { APIProvider, Map } from '@vis.gl/react-google-maps';

interface MapViewProps {
  pickupCoords?: { lat: number; lng: number };
  dropoffCoords?: { lat: number; lng: number };
}

export default function MapView({ pickupCoords, dropoffCoords }: MapViewProps) {
  const apiKey = process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY || '';

  // Default center: Orange County / Los Angeles area
  const defaultCenter = { lat: 33.6846, lng: -117.8265 };

  // Calculate center - prioritize pickup, then dropoff, then default
  const center = pickupCoords || dropoffCoords || defaultCenter;

  if (!apiKey) {
    return (
      <div className="absolute inset-0 w-full h-full bg-gray-200 flex items-center justify-center">
        <div className="text-center p-8 bg-white rounded-lg shadow-lg max-w-md">
          <svg className="w-16 h-16 mx-auto mb-4 text-red-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
          </svg>
          <h3 className="text-lg font-bold text-gray-800 mb-2">
            ÈúÄË¶Å Google Maps API Key
          </h3>
          <p className="text-sm text-gray-600 mb-4">
            ËØ∑Âú® .env Êñá‰ª∂‰∏≠Ê∑ªÂä†Ôºö<br />
            <code className="bg-gray-100 px-2 py-1 rounded text-xs">
              NEXT_PUBLIC_GOOGLE_MAPS_API_KEY
            </code>
          </p>
          <a
            href="https://console.cloud.google.com/google/maps-apis"
            target="_blank"
            rel="noopener noreferrer"
            className="text-blue-600 hover:underline text-sm font-medium"
          >
            Ëé∑ÂèñÂÖçË¥π API Key ‚Üí
          </a>
        </div>
      </div>
    );
  }

  return (
    <div className="absolute inset-0 w-full h-full">
      <APIProvider apiKey={apiKey}>
        <Map
          defaultCenter={center}
          defaultZoom={11}
          gestureHandling="greedy"
          disableDefaultUI={false}
        />
      </APIProvider>
    </div>
  );
}
